import React ,{useState} from 'react'
import axios from 'axios'
import './Weather.css'
import img from '../assets/background.jpg'
import dark from '../assets/cloudy.jpg'
export default function WeatheScreen() {
    const [searchq, setSearchQ]=useState('');
    const [weatherdata, setWeatherData]=useState('')
    const [darkthemeflag, setDarkthemeFlag]=useState(false)
    const Search= async(e)=>{
     
if(e.key==="Enter"){
  axios.get(`https://api.openweathermap.org/data/2.5/weather?q=${searchq}&appid=ad2d4c031975987b4e445a7109743c6b`)
  .then(res=>
    {

    setWeatherData(res.data)
 
  })

}
        }
 
    return (

        
  <div className="main" style={{backgroundColor:darkthemeflag?'#000':'#fff'}}>
    <div className="container" style={{backgroundImage:darkthemeflag?`url(${dark})`:`url(${img})`,backgroundPosition:"center",backgroundSize:"cover",boxShadow:darkthemeflag?' 3px 3px 20px rgba(207, 204, 204, 0.2':'4px 4px 15px rgba(0, 0, 0, 0.7)',color:darkthemeflag?'#fff':'#242323'}}>
     <input type="text" placeholder="search...." value={searchq} onChange={e=> setSearchQ(e.target.value)} onKeyPress={Search} className="search"/>   
   
        {(typeof weatherdata.main!="undefined")?( <div className="weather-data"><h4>{weatherdata.name},{weatherdata.sys.country}</h4>
    
     
    <p> Weather Condition:<br/><br/>{weatherdata.weather[0].description}</p>
        <div className="temperature" style={{color:darkthemeflag?'#fff':'#242323'}}>   {Math.round(weatherdata.main.temp)}Â°c
        </div></div>):(  '')}
        <div className="moon" onClick={()=>setDarkthemeFlag(!darkthemeflag)} ><i class="fas fa-moon"></i></div>
       
    </div>
    </div>

        
    )
}
